:root {
    --hack-main: #0C2724;
    --summit-main: #f300fc; 
}

.card {
    border-radius: 3px !important;
    border: 1px solid lightgrey;
    z-index: 500;
    -webkit-box-shadow: 4px 6px 4px -1px #00000031; 
    box-shadow: 4px 6px 4px -1px #00000031;
  }
  

.card-title {
    font-family: Arial, sans-serif;
    font-weight: 600;
}

.img-container {
    width: 100%;
    position: absolute;
    top: 0;
    display: flex;
    align-content: center;
    justify-content: center;
}

.post-img {
    z-index: 1100;
    top: 0;
}

.post-description {
    font-family: Arial, sans-serif;
    font-size: 14px;
    text-align: justify;
    color: black;
}


.post-img > svg {
    width: 100%;
    border-radius: 3px 3px 0 0;
}

.btn {
    width: 100%;
    background-color: white;
    border: solid lightgray 1px;
    color: lightgray;
    font-weight: 700;
}

.btn:hover {
    background-color: white;
    border: solid lightgray 1px;
    color: lightgray;
    font-weight: 700;
}

.btn.active {
    background-color: var(--hack-main);
    border: solid var(--hack-main) 1px;
}


.btn:not(:last-child) {
    margin: 8px 0;
}

  
.btn.active:hover {
background-color: white;
border: solid var(--hack-main) 1px;
color: var(--hack-main);
}

.file-input {
    display: none;
}

i {
    margin: 0 8px;
    color: var(--hack-main);
}





input[type=range] {
    -webkit-appearance: none;
    margin: 18px 0;
    width: 100%;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    height: 1.4px;
    cursor: pointer;
    background: var(--hack-main);
    border-radius: 1.3px;
    border: 0.2px solid var(--hack-main);
  }
  input[type=range]::-webkit-slider-thumb {
    border: 1px solid var(--hack-main);
    height: 20px;
    width: 20px;
    border-radius: 10px;
    background: #ffffff;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -10px;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: var(--hack-main);
  }
  input[type=range]::-moz-range-track {
    height: 1.4px;
    cursor: pointer;
    background: var(--hack-main);
    border-radius: 1.3px;
    border: 0.2px solid var(--hack-main);
  }
  input[type=range]::-moz-range-thumb {
    border: 1px solid var(--hack-main);
    height: 20px;
    width: 20px;
    border-radius: 10px;
    background: #ffffff;
    cursor: pointer;
    margin-top: -10px;
  }
  input[type=range]::-ms-track {
    color: transparent;
    height: 1.4px;
    cursor: pointer;
    border-radius: 1.3px;
    background: transparent;
    border-color: transparent;
    border-width: 16px 0;
  }
  input[type=range]::-ms-fill-lower {
    background: var(--hack-main);
    border: 0.2px solid var(--hack-main);
    border-radius: 2.6px;
  }
  input[type=range]::-ms-fill-upper {
    background: var(--hack-main);
    border: 0.2px solid var(--hack-main);
    border-radius: 2.6px;
  }
  input[type=range]::-ms-thumb {
    border: 1px solid var(--hack-main);
    height: 20px;
    width: 20px;
    border-radius: 10px;
    background: #ffffff;
    cursor: pointer;
  }
  input[type=range]:focus::-ms-fill-lower {
    background: var(--hack-main);
  }
  input[type=range]:focus::-ms-fill-upper {
    background: var(--hack-main);
  }