.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.main {
  min-height: 100vh;
  height: auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap-reverse;
  justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  color: black;
  width: 100%;
}

#bg-video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%; 
  min-height: 100%;
  z-index: 100;
}

#bg-image {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%; 
  min-height: 100%;
  opacity: 0.3;
  z-index: 200;
}

.disclaimer {
  position: relative;
  width: 100%;
  background-color: black;
  bottom: 0;
  z-index: 2000;
  color: white !important;
  border-top: solid 1px white;
  font-size: x-small;
  padding: 0 15px;
}



.info-box {
  color:white;
  z-index: 2000;
  display: block;
  margin: 35px;
}

.info-box > h1 {
  font-family: "AvenirNextPro", sans-serif;
  font-weight: 800;
  font-size: 4.2rem;
}

.info-box > p {
  font-family: "AvenirNextPro", sans-serif;
  font-weight: 500;
}