@font-face {
  font-family: 'AvenirNextPro';
  font-weight: 800;
  src: local('AvenirNextPro_Heavy'), url(./fonts/avenir_next_heavy.woff) format('woff'), url(./fonts/avenir_next_heavy.ttf) format('ttf');
}

@font-face {
  font-family: 'AvenirNextPro';
  font-weight: 700;
  src: local('AvenirNextPro_Medium'), url(./fonts/avenir_next_medium.woff) format('woff'), url(./fonts/avenir_next_medium.ttf) format('ttf');
}

@font-face {
  font-family: 'AvenirNextPro';
  font-weight: 500;
  src: local('AvenirNextPro_Thin'), url(./fonts/avenir_next_thin.woff) format('woff'), url(./fonts/avenir_next_thin.ttf) format('ttf');
}



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
